import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import {
  OrderStateCategoryEnum,
  useQKitchenOrderList,
  useSKitchenOrderList,
} from '../../../graph/generated'
import { QKitchenOrderList, SKitchenOrderList } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { KitchenOrderBox } from '../molecules/KitchenOrderBox'

interface IProps {
  category: OrderStateCategoryEnum
}

const InfoMessage = styled.div`
  margin: 0 0.5rem;
`

export const KitchenOrderList = observer<IProps>(({ category }) => {
  const { order: orderModel } = useRoot()

  const { data, loading, updateQuery } = useQKitchenOrderList({
    branches: orderModel.list.branchesFilter,
    categories: [category],
  })

  useSKitchenOrderList(
    {
      branches: orderModel.list.branchesFilter,
      categories: [category],
    },
    {
      onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
        if (dataUpdate) {
          updateQuery(prev => {
            return handleSubscriptionUpdate(dataUpdate, prev)
          })
        }
      },
    },
  )

  if (loading) {
    return (
      <InfoMessage>
        <Trans>Loading</Trans>
      </InfoMessage>
    )
  }

  const orders = data.orders.filter(RM.propEq('orderStateCategory', category))

  if (orders.length === 0) {
    switch (category) {
      case OrderStateCategoryEnum.NewOrders:
        return (
          <InfoMessage>
            <Trans>No new orders</Trans>
          </InfoMessage>
        )
      case OrderStateCategoryEnum.Preparing:
        return (
          <InfoMessage>
            <Trans>No orders to prepare</Trans>
          </InfoMessage>
        )
      case OrderStateCategoryEnum.ReadyToDeliver:
        return (
          <InfoMessage>
            <Trans>No orders to deliver</Trans>
          </InfoMessage>
        )
      default:
        return (
          <InfoMessage>
            <Trans>No orders</Trans>
          </InfoMessage>
        )
    }
  }

  return (
    <>
      {orders.map(order => (
        <KitchenOrderBox
          key={order.id}
          order={order}
          showBranch={orderModel.list.branchesFilter.length > 1}
        />
      ))}
    </>
  )
})

function handleSubscriptionUpdate(
  update: SKitchenOrderList.Subscription,
  prev: QKitchenOrderList.Query,
) {
  const { order } = update.updatedOrder
  if (!order) {
    return prev
  }
  let orderExists = false
  const orders = prev.orders.map(o => {
    if (o.id === order.id) {
      orderExists = true
      return order
    }
    return o
  })
  if (!orderExists) {
    orders.unshift(order)
  }
  return { ...prev, orders }
}
